import {PerformanceEntry} from './PerformanceEntry';

                               

                                      
                      
                                  
  

                                                           

                                      
                      
                                 
                                
  

export class PerformanceMark extends PerformanceEntry {
  #detail            ;

  constructor(markName        , markOptions                         ) {
    super({
      name: markName,
      entryType: 'mark',
      startTime: markOptions?.startTime ?? performance.now(),
      duration: 0,
    });

    if (markOptions) {
      this.#detail = markOptions.detail;
    }
  }

  get detail()             {
    return this.#detail;
  }
}

export class PerformanceMeasure extends PerformanceEntry {
  #detail            ;

  constructor(measureName        , measureOptions                        ) {
    super({
      name: measureName,
      entryType: 'measure',
      startTime: measureOptions.startTime,
      duration: measureOptions.duration,
    });

    if (measureOptions) {
      this.#detail = measureOptions.detail;
    }
  }

  get detail()             {
    return this.#detail;
  }
}
